* {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  text-align: center;
}
body {
  margin: 100px;
  background-color: rgb(255, 255, 255);
}
.to-do{
  background-color: rgb(236, 236, 236);
  text-align: center;
  width: 100%;
  border-radius: 7px;

}
input{
  width:80% ;
  font-size: 17px;
  height: 40px;
  outline: none;
  border-radius:4px ;
}
button{
  border: none;
  box-shadow: inset 1px  1px 4px black;
  padding: 1px 1px;
  height: 45px;
  margin: 3px;
  border-radius: 4px;
}
ol{
  padding: 0;
}
li{
  display: flex;
  align-items: center;
  margin: 2px;
  border: 1px black solid;
  color: rgb(0, 0, 0);
  background-color: rgb(252, 252, 252);
  border-radius: 4px;
}
.text{
  flex: 1;

}
.delete-but , .move-but{
  padding: 5px 8px;
  font-size: 10px;
}
.delete-but{
  background-color: brown;
}
.move-but{
  background-color: aqua;
}
.add-button{
  background-color: rgb(95, 196, 27);
}
span{
  font-size: small;
}
h1{
  transform: rotate(0deg);
  color: rgb(0, 0, 0);
  border-radius: 30px;
}